import React from 'react';
import { Link } from 'react-router-dom';
import './SheltersOnMain.css';
import shelterLogo from '../../images/shelter-logo.svg';
import paw from '../../images/paw.svg';

const SheltersOnMain = () => {
  return (
    <section className='shelters-on-main'>
      <h2 className='section-title'>,    </h2>
      <ul className='shelters-on-main__shelters-container'>
        <li className='shelter-card'>
          <img className='shelter-card__logo' src={shelterLogo} alt=' ' />
          <h3 className='shelter-card__title'> </h3>
          <p className='shelter-card__address'> -, 10, , </p>
          <p className='shelter-card__working-hours'> : <span className='shelter-card__hours'>10:00 - 18:00</span></p>
          <Link className='shelter-card__link' to='/shelters'> </Link>
          <img className='shelter-card__paw' src={paw} alt='' />
        </li>
        <li className='shelter-card'>
          <img className='shelter-card__logo' src={shelterLogo} alt=' ' />
          <h3 className='shelter-card__title'> </h3>
          <p className='shelter-card__address'> -, 10, , </p>
          <p className='shelter-card__working-hours'> : <span className='shelter-card__hours'>10:00 - 18:00</span></p>
          <Link className='shelter-card__link' to='/shelters'> </Link>
          <img className='shelter-card__paw' src={paw} alt='' />
        </li>
        <li className='shelter-card'>
          <img className='shelter-card__logo' src={shelterLogo} alt=' ' />
          <h3 className='shelter-card__title'> </h3>
          <p className='shelter-card__address'> -, 10, , </p>
          <p className='shelter-card__working-hours'> : <span className='shelter-card__hours'>10:00 - 18:00</span></p>
          <Link className='shelter-card__link' to='/shelters'> </Link>
          <img className='shelter-card__paw' src={paw} alt='' />
        </li>
      </ul>
      <button className='button margin-left_auto' to='/map'>  </button>
      <div className='shelters-on-main__text-container'>
        <h3 className='shelters-on-main__text-title'>  ?</h3>
        <p className='shelters-on-main__text-subtitle'>        !</p>
        <button className='button margin_column-center' disabled> </button>
      </div>
    </section>
  );
}

export default SheltersOnMain;
